h1, h2, h3, h4 {
    font-family: 'Cormorant Garamond', serif; }
h1 {
    font-size: 6vh;
    line-height: 1.66;
    margin-bottom: 15vh;
    margin-top: 30vh; }
h2 {
    font-size: 2rem;
    margin-bottom: 0;
    padding-top: 5vh; }
h3 {
    font-size: 1.6rem; }
h4 {
    font-size: 1.4rem;
    margin-bottom: 0;
    & + .images {
        margin-top: 1rem; }
    & + .images p {
        margin-top: 1rem; } }
p {
    color: $text65;
    line-height: 1.66; }
