form {
    display: flex;
    flex-direction: column;
    width: 100%;
    label {
        color: $text65;
        font-size: 0.8rem; }
    input[type=text], input[type=email], textarea {
        font-family: 'Inter', sans-serif;
        color: $text35;
        border: none;
        border-bottom: 1px solid $text05;
        font-size: 1rem;
        line-height: 2rem;
        margin: 1rem;
        margin-left: 0; }
    input[type=text]:focus, input[type=email]:focus, textarea:focus {
        color: $text95;
        border: none;
        border-bottom: 1px solid $text65; }
    button {
        background: $bg;
        color: $text95;
        font-size: 1rem;
        line-height: 1.5rem;
        padding: 0.75rem 1.15rem;
        border: 1px solid $text95;
        border-radius: 3px;
        width: 40%;
        align-self: center;
        &:hover {
            background: $text95;
            color: $bg;
            cursor: pointer; } } }
