body {
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    padding: 0 8vw;
    color: $text95;
    @include bp-small {
        padding: 0 18px; } }
header {
    width: 60%;
    @include bp-small {
        width: 100%; } }
hr {
    color: $text95;
    width: 100%; }
nav {
    height: 5vh;
    width: 84vw;
    @include bp-small {
        width: 100%; }
    ul {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        //@include bp-small
        //  justify-content: space-between
        li {
            list-style: none;
            padding: 0 2vw;
            a {
                color: inherit;
                &:hover {
                    color: blue; } } } }
    top: 5vh; }
.case-studies {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .case-study {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        @include bp-small {
            padding: 2rem 0; } }
    a {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        &:hover {
            background: $text02; } } }
.about {
    padding: 2rem 6rem;
    @include bp-small {
        padding: 1rem 0; } }
.contact {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .contact-form, .contact-list {
        flex: 1;
        display: flex;
        justify-content: top;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        @include bp-small {
            padding: 2rem 0; }
        ul {
            padding-left: 0;
            li {
                list-style: none;
                line-height: 3; } } } }
.images {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0;
    img {
        object-fit: cover;
        width: 100%; }
    p {
        font-size: 0.9rem;
        font-style: italic;
        margin-top: 0; } }
footer {
    color: $text35;
    padding: 5vh 8vw;
    text-align: center;
    @include bp-small {
        padding: 5vh 0; } }
